import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect, useState } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFSelect } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
BankTransferSelInt.propTypes = {
    settings: PropTypes.object,
};
export default function BankTransferSelInt({ settings }) {
    const isMountedRef = useIsMountedRef();
    const [allvend, SetVend] = useState([{ key: 'monnify', name: 'Monnify' }]);

    const NewUserSchema = Yup.object().shape({
        bank_transfer: Yup.string().required('BANK TRANSFER VENDING REQUIRED'),
    });

    const defaultValues = useMemo(
        () => ({
            bank_transfer: settings?.bank_transfer?.bank_transfer || 'smart',
        }),
        [settings]
    );

    const AccessToken = window.localStorage.getItem('accessToken');
    const methods = useForm({
        resolver: yupResolver(NewUserSchema),
        defaultValues,
    });

    const {
        reset,
        setError,
        handleSubmit,
        formState: { errors, isSubmitting },
    } = methods;

    useEffect(() => {
        if (settings !== undefined && settings.bank_transfer) {
            const formData = {
                bank_transfer: settings.bank_transfer.bank_transfer || 'smart',
            };
            reset(formData);
            if (settings.transfer_providers) {
                SetVend(settings.transfer_providers);
            }
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [settings]);

    const onSubmit = async (data) => {
        try {
            await axios.post(`/api/edit/bank-transfer/sel/account/${AccessToken}/habukhan/secure`, data);
            swal('Success', 'Bank Transfer Vending Updated', 'success');
        } catch (error) {
            console.error(error);
            swal('Error', 'Unable to update settings', 'error');
        }
    };

    return (
        <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
            <Grid container spacing={3}>
                <Grid item xs={12} md={8}>
                    <Card sx={{ p: 3 }}>
                        <Box
                            sx={{
                                display: 'grid',
                                columnGap: 2,
                                rowGap: 3,
                                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
                            }}
                        >
                            <RHFSelect name="bank_transfer" label="Primary Bank Transfer Vending" placeholder="Primary Bank Transfer Vending">
                                <option value="" />
                                <option value="smart">Smart Routing (Fallback enabled)</option>
                                {allvend && allvend.map((option) => (
                                    <option key={option.key} value={option.key}>
                                        {option.name || option.key}
                                    </option>
                                ))}
                            </RHFSelect>
                        </Box>

                        <Stack alignItems="flex-end" sx={{ mt: 3 }}>
                            <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                                Save Changes
                            </LoadingButton>
                        </Stack>
                    </Card>
                </Grid>
            </Grid>
        </FormProvider>
    );
}
