import { useState, useEffect } from 'react';
// @mui
import { Container } from '@mui/material';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import BankTransferSelInt from '../../sections/admin/BankTransferSelInt';
import axios from '../../utils/axios';

// ----------------------------------------------------------------------

export default function BankTransferSel() {
    const { themeStretch } = useSettings();
    const [discount, setDiscount] = useState();

    const AccessToken = window.localStorage.getItem('accessToken');

    useEffect(() => {
        async function fetchData() {
            try {
                const response = await axios.get(`/api/bank-transfer/sel/by/system/${AccessToken}/secure`);
                setDiscount(response.data);
            } catch (error) {
                console.error(error);
            }
        }
        fetchData();
    }, [AccessToken]);

    return (
        <Page title="Bank Transfer Vending Selection">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading="Bank Transfer Vending Selection"
                    links={[
                        { name: 'Dashboard', href: PATH_ADMIN.root },
                        { name: 'Bank Transfer Vending Selection' },
                    ]}
                />
                <BankTransferSelInt settings={discount} />
            </Container>
        </Page>
    );
}
